.all{
    position: relative;
    width: 100%;
    height: 100%;
    background: url(background.jpg);
    background-size: cover;
    background-position: center;
}
.username{
    position: absolute;
    top: 40%;
    width: 400px;
    right: 5%;
    padding: 10px;
    text-align: center;
    color: whitesmoke;
    font-size: 1.1em;
}
#user{
    font-size: 1.3em;
    font-weight: 600;
    margin-bottom: 10px;
   
}
@media only screen and (max-width:500px){
    .username{
        position: absolute;
        width: 200px;
        right: 5%;
        padding: 5px;
        font-size: 1.0em;
    }
    #user{
        font-size: 1.2em;
    }
}