* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}

body {
    position: relative;
    width: 100vw;
    height: 100vh;
    overflow: hidden;
}

.nav {
    position: absolute;
    top: 1px;
    width: 100%;
    height: 60px;
    padding: 0px 10px;
    background: rgba(0, 0, 0, 0.8);
    -webkit-backdrop-filter: blur(10px);
    backdrop-filter: blur(10px);
    display: flex;
    justify-content: space-between;
    align-items: center;
}

.nav ul {
    padding: 5px 10px;
    display: flex;
}

.nav ul li {
    list-style: none;
    margin: 2px 10px;
    color: rgb(239, 229, 229);
    cursor: pointer;
    transition: 0.1;
    padding: 2px 6px;
    border-radius: 5px;
    font-size: 1em;
    text-align: center;
}

.nav ul li:hover {
    color: rgb(22, 232, 239);
}

.active li {
    background: rgb(4, 133, 231);
    color: aqua;
    transform: scale(1.1);
}
.active li:hover {
    color: aliceblue;
}
.nav .profile {
    margin-right: 10px;
    padding: 2px 6px;
    font-size: 1.1em;
    color: aliceblue;
    font-weight: 600;
}

.allcontent {
    position: relative;
    overflow: hidden;
    display: flex;
    justify-content: center;
    align-items: center;
    top: 60px;
    left: 0;
    width: 100%;
    height: calc(100vh - 60px);
}
.toogle{
    /* background: red; */
    width: 100%;
    padding: 5px;
    text-align: center;
    margin: 10px 0px;
}
.toogle .here{
    color: rgb(6, 99, 106);
    font-size: 1.3em;
    cursor: pointer;
    text-decoration: none;
    margin: 10px 20px;
    padding: 4px 8px;
    border-radius: 10px;
}

@media only screen and (max-width:500px){
    .nav ul {
        padding: 5px;
    }
    .nav ul li {
        margin: 2px;
        padding: 2px 4px;
        border-radius: 5px;
        font-size: 0.9em;
    }
}