
.container {
    background: #e8edf1;
    box-shadow: 10px 10px 10px #cbced1,
        -13px -13px 15px #fff;
    position: relative;
    width: 650px;
    height: 600px;
    border-radius: 15px;
    padding: 10px;
    display: flex;
    flex-direction: column;
    align-items: center;
}
h1 {
    color: rgb(13, 123, 151);
}
.add {
    background: #bdc3c7;
    border-radius: 50%;
    transition: 0.2s;
    transform: scale(1.5);
    color: rgb(7, 131, 156);
    padding: 3px;
    margin: 0px 8px;
    cursor: pointer;
}
.add:hover {
    background: #3498db;
    color: white;
    transform: scale(1.4);
}
.command {
    display: flex;
    position: relative;
    border: 2px solid rgb(10, 179, 247);
    width: 100%;
    border-radius: 10px;
    display: flex;
    justify-content: space-around;
    align-items: center;
    padding: 5px 5px;
    margin-top: 10px;
    background: #ecf0f3;
    box-shadow: 8px 8px 10px #cbced1,
        -8px -8px 10px #fff;
}

.notify {
    margin: 8px 0px;
    padding: 5px 10px;
    border-radius: 5px;
    position: relative;
    width: 100%;
    display: flex;
    justify-content: space-around;
    align-items: center;
}

.card {
    width: 32%;
    border-radius: 10px;
    cursor: pointer;
    padding: 4px;
    transition: 0.2s;
    display: flex;
    height: 90px;
    overflow: hidden;
    background-image: linear-gradient(179.4deg, rgba(33, 150, 243, 1) 1.8%, rgba(22, 255, 245, 0.60) 97.1%);
    color: #fff;
}
.card:hover{
    transform: scale(1.02);
    box-shadow: 3px 3px 2px #cbced1,
    -3px -3px 2px #fff;
}
.card:nth-child(2) {
    background-image: radial-gradient(circle 597px at 93% 9.8%, rgba(255, 61, 89, 1) 1.7%, rgba(252, 251, 44, 1) 97%);
}

.card:nth-child(3) {
    background-image: linear-gradient(109.6deg, rgba(39, 142, 255, 1) 11.2%, rgba(98, 113, 255, 0.78) 100.2%);
}

.card .first {
    width: 60%;
}

.card .first div:nth-child(1),
.card .first div:nth-child(2) {
    height: 50%;
    padding: 4px;
    padding-left: 10px;
}

.card .first div:nth-child(1) {
    font-size: 1.8em;
    font-weight: 600;
    text-shadow: 2px 2px 3px rgba(0, 0, 0, 0.3);
}

.card .first div:nth-child(2) {
    font-size: 1.2em;
    font-weight: 600;
    text-shadow: 2px 2px 3px rgba(0, 0, 0, 0.3);
}

.card .second {
    border-radius: 20px;
    display: flex;
    justify-content: center;
    align-items: center;
    transform: scale(1.6);
    width: 40%;
}

.li {
    width: 100%;
    position: relative;
    overflow: auto;
}

.li div {
    position: relative;
    width: 98%;
}

.income,
.exp,
#ledger {
    padding-left: 10px;
    margin: 9px 0px;
    border-radius: 5px;
    width: 100%;
    padding: 8px 4px;
    background: #e8edf1;
    box-shadow: 3px 3px 2px #cbced1,
        -3px -3px 2px #fff;
}

.exp {
    border-left: 5px solid rgb(238, 113, 4);
    color: rgb(238, 113, 4);
}

.income {
    border-left: 5px solid #2980b9;
    color: #2980b9;
}

.income span:nth-child(2),
.exp span:nth-child(2) {
    /* background: #2980b9; */
    display: inline-block;
    width: 80px;
}

.income span:nth-child(3),
.exp span:nth-child(3) {
    /* background: #2980b9; */
    margin-left: 20px;
    padding-left: 5px;
    display: inline-block;
    width: 80px;
}

.del,
.edi {
    position: absolute;
    right: 10px;
    top: 50%;
    transform: translateY(-50%);
    color: grey;
    transition: 0.1s;
    cursor: pointer;
}

.del:hover {
    color: #ff7675;
    transform: translateY(-50%) scale(1.1);
}

.edi {
    right: 50px;
}

.edi:hover {
    color: #00cec9;
    transform: translateY(-50%) scale(1.1);
}

.alert {
    width: 100%;
}

.jai {
    position: absolute;
    top: 5px;
    right: 5px;
    padding: 2px;
}

@media only screen and (max-width:500px) {
    .container {
        box-shadow: 10px 10px 10px #cbced1,
            -10px -10px 10px #fff;
        position: relative;
        width: 95vw;
        max-height: 95vh;
        border-radius: 15px;
        padding: 6px;
    }

    h1 {
        font-size: 1.2em;
    }

    .income span:nth-child(2),
    .exp span:nth-child(2) {
        width: 60px;
        font-size: 0.8em;
    }
    
    .income span:nth-child(3),
    .exp span:nth-child(3) {
        margin-left: 5px;
        padding-left: 1px;
        display: inline-block;
        width: 60px;
        font-size: 0.9em;
    }
    .income span:nth-child(4),
    .exp span:nth-child(4) {
        width: 60px;
        font-size: 0.8em;
    }

    .notify {
        margin: 5px 0px;
        padding: 5px;
        flex-wrap: wrap;
    }

    .card {
        width: 30%;
        border-radius: 10px;
        padding: 3px;
        height: 60px;
    }

    .card .first {
        width: 60%;
    }

    .card .first div:nth-child(1),
    .card .first div:nth-child(2) {
        height: 50%;
        padding: 4px;
        padding-left: 4px;
    }

    .card .first div:nth-child(1) {
        font-size: 1.2em;
    }

    .card .first div:nth-child(2) {
        font-size: 0.9em;
    }

    .card .second {
        transform: scale(0.8);
        width: 40%;
    }
}